<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/home-01.png')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Soporte técnico</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">Chat</div>
        </v-col>
      </v-row>

      <chat key-field="channel_id"
            :key-value="user.id"
            :event-name="'channel-id-' + user.id"
            event-class="App\Events\MessageAdded"
            :get-messages-url="getMessagesUrl"
            :post-messages-url="postMessagesUrl"></chat>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import Chat from '../../components/Chat'
import axios from 'axios'

export default {
  name: 'SupportChat',
  components: {Chat},
  computed: {
    ...mapGetters(['user']),
    getMessagesUrl() {
      return '/messages?media=support'
    },
    postMessagesUrl() {
      return '/messages?media=support'
    },
  },
  mounted() {
    this.getWelcomeMessage()
  },
  methods: {
    getWelcomeMessage() {
      axios.get('messages/' + this.user.id + '/welcome?media=support')
    },
  }
}
</script>
